.slowlyAppear {
  animation: slowlyAppear 0.3s linear;
}

.edge {
  background: saddlebrown !important; 
  border-collapse: collapse !important;
  border: hidden !important;
  border: 0pt !important;
  opacity: 0.8;
  /*-webkit-animation-name: highlight_last_move; Chrome, Safari, Opera */
  /*-webkit-animation-duration: 1s; Chrome, Safari, Opera */
  animation-name: highlight_last_move;
  animation-duration: 0.1s ease;
}

.edge_new {
  background: #FF6600 !important; 
  border-collapse: collapse !important; 
  border: 0pt;
  /*-webkit-animation-name: highlight_last_move; Chrome, Safari, Opera */
  /*-webkit-animation-duration: 1s; Chrome, Safari, Opera */
  animation-name: highlight_last_move_new;
  animation-duration: 0.01s;
}

@keyframes highlight_last_move {
    from {background-color: black;}
    to {background-color: saddlebrown;}
}
@keyframes highlight_last_move_new {
    from {background-color: yellow;}
    to {background-color: #FF6600;}
}

.pl0 {
  background: #ff9933; 
  /*border: 1px solid #959595;*/
}

.pl1 {
  background: #0099ff; 
  /*border: 1px solid #959595;*/
}

.dot {
  background: #bfbfbf; 
  box-shadow: inset 0 0 35px 5px rgba(0,0,0,0.25), inset 0 2px 1px 1px rgba(255,255,255,0.9), inset 0 -2px 1px 0 rgba(0,0,0,0.25);
}

.dot:hover{
  -moz-transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);  
  -webkit-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  -ms-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
  box-shadow: inset 0 0 30px 5px rgba(0,0,0,0.25), inset 0 2px 1px 1px rgba(255,255,255,0.9), inset 0 -2px 1px 0 rgba(0,0,0,0.25);
}

