.slowlyAppear {
	-webkit-animation: slowlyAppear 0.03s linear;
	-moz-animation: slowlyAppear 0.03s linear;
	-ms-animation: slowlyAppear 0.03s linear;
	-o-animation: slowlyAppear 0.03s linear;
	animation: slowlyAppear 0.03s linear;
}

.line:not(:active):hover{
  border-collapse: collapse;
  border: 1px groove;
  border-spacing: 0;
  /*fill:darkred; 
  opacity: 0.3;*/
  /*animation: hideshow 0.3s ease infinite;*/
}

.line:active{
  background: black;
  opacity: 0.6;
  border: inset;
  transform: scale(1.1);
}

@keyframes hideshow {
  0% { opacity: 1; }
  10% { opacity: 0.7; }
  15% { opacity: 0.3; }
  100% { opacity: 0; }
} 

@-webkit-keyframes slowlyAppear {
  from {opacity: 0.1;}
  to {opacity: 1;}
}
@-moz-keyframes slowlyAppear {
  from {opacity: 0.1;}
  to {opacity: 1;}
}
@-o-keyframes slowlyAppear {
  from {opacity: 0.1;}
  to {opacity: 1;}
}
@keyframes slowlyAppear {
  from {opacity: 0.1;}
  to {opacity: 1;}
}

@keyframes moveUp {
    0%   {background-color:red; left:0%; top:0%;}
    25%  {background-color:yellow; left:10%; top:0px;}
    50%  {background-color:blue; left:10%; top:10%;}
    75%  {background-color:green; left:0px; top:10%;}
    100% {background-color:red; left:0%; top:0%;}
}

td {
  padding:0 3px 0 3px;
}

